import { apiClient } from "./Service";

export default {
  async getAllPersonNotifications(token, page, data) {
    let uri = `/notifications/get/person/notifications?page=${page}&limit=10`;

    if (data && data.type) uri += `&type=${data.type}`;
    if (data && data.target) uri += `&target=${data.target}`;
    if (data && data.isWatched) uri += `&isWatched=${data.isWatched}`;
    return await apiClient.get(uri, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async toggleNotificationIsWatched(token, id) {
    return await apiClient(`/notifications/toggle/watched/${id}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
      method: "PATCH",
    });
  },
};
