import AllBookRequests from "@/views/book_requests/AllBookRequests.vue";
import EditBookRequest from "@/views/book_requests/EditBookRequest.vue";
import ShowBookRequest from "@/views/book_requests/ShowBookRequest.vue";

export const bookRequestRoutes = [
  { path: "/book_requests", name: "all-book_requests", component: AllBookRequests },
  { path: "/book_requests/:id", name: "show-book_requests", component: ShowBookRequest, props: true },
  { path: "/book_requests/:id/edit", name: "edit-book_requests", component: EditBookRequest, props: true },
];
