<template lang="pug">
BodyComponent
  #main
    .row
      C-Breadcrump(
        :title="breadcrumpTitle"
        start="خانه"
        :startPath="{ name: 'home-page' }"
        middle="سفارش‌ها"
        :middlePath="{ name: 'all-book_requests' }"
        last="نمایش سفارش"
      )
      .col.s12
        .container
          // users view start
          .section.users-view
            // users view media object start
            .card-panel
              .row
                .col.s12.m7
                  .display-flex.media
                    a.avatar(href="#")
                      img.z-depth-1.circle(
                        :src="[bookRequest.request_image ? globals.baseURL + '/' + bookRequest.request_image : '/app-assets/images/avatar/guest.png']",
                        alt="users view avatar",
                        height="64",
                        width="64"
                      )
                    .media-body
                      h6.media-heading
                        span.users-view-username.grey-text.ml-3.mr-3 {{ user.mobile }}
                        span.users-view-name.indigo-text {{ bookRequest.target_name }}
                      span شناسه:
                      span.en.users-view-id.pink-text.ml-3 {{ bookRequest.id }}
                .col.s12.m5.quick-action-btns.display-flex.justify-content-end.align-items-center.pt-2
                  router-link.btn-small.indigo(:to="{ name: 'edit-book_requests', props: { id: bookRequest.id } }") ویرایش
            // users view media object ends
            // users view card data start
            .card
              .card-content
                .row
                  .col.s12.m4
                    table.responsive-table.striped
                      tbody
                        tr
                          td شماره :
                          td {{ bookRequest.no }}
                        tr
                          td ورژن :
                          td {{ bookRequest.version }}
                        tr
                          td زمان ایجاد :
                          td {{ toPersianTime(bookRequest.created_at) }}
                        tr
                          td زمان بروزرسانی :
                          td.users-view-latest-activity {{ toPersianTime(bookRequest.updated_at) }}
                        tr
                          td زمان حذف :
                          td
                            span.chip.lighten-5.users-view-latest-activity.m-0(:class="bookRequest.deleted_at === null ? 'green green-text' : 'red red-text' ") {{ bookRequest.deleted_at === null ? 'حذف نشده' : toPersianTime(bookRequest.deleted_at) }}
                  .col.s12.m4
                    table.responsive-table.striped
                      tbody
                        tr
                          td مبلغ پرداخت شده :
                          td {{ bookRequest.request_price }}
                        tr
                          td نوع :
                          td
                            span.chip.lighten-5.m-0(:class="bookRequest.type === 'PUBLIC' ? 'green green-text' : 'red red-text'") {{ bookRequest.type === "PUBLIC" ? 'عمومی' : 'خصوصی' }}
                        tr
                          td سفارش برای :
                          td
                            span.chip.lighten-5.m-0(:class="bookRequest.target === 'SELF' ? 'indigo indigo-text' : 'cyan cyan-text'") {{ bookRequest.target === "SELF" ? 'خودم' : 'دوستم' }}
                        tr
                          td ضمیر :
                          td
                            span.chip.lighten-5.blue.blue-text.m-0 {{ bookRequest.pronoun }}
                        tr
                          td مناسبت :
                          td
                            span.chip.lighten-5.pink.pink-text.m-0 {{ bookRequest.whats_for }}
                  .col.s12.m4
                    table.responsive-table.striped
                      tbody
                        tr
                          td درصد هدیو :
                          td {{ bookRequest.commission }}
                        tr
                          td درصد پلن :
                          td {{ bookRequest.plan }}
                        tr
                          td نام پلن :
                          td
                            span.chip.lighten-5.m-0(:class="bookRequest.plan_type !== 'NOT_FOUND' ? 'green green-text' : 'red red-text'") {{ bookRequest.plan_type }}
                        tr
                          td وضعیت سفارش :
                          td
                            span.chip.lighten-5.m-0(:class="bookRequest.isAccepted === 'NOT_ANSWERED' ? 'green green-text' : 'blue blue-text' ") {{ bookRequest.isAccepted }}
                        tr
                          td دلیل رد کردن :
                          td
                            span.m-0(:class="bookRequest.reject_reason !== null ? 'chip lighten-5 red red-text' : ''") {{ bookRequest.reject_reason }}
                        tr
                          td دارای تخفیف :
                          td
                            span.chip.lighten-5.m-0(:class="bookRequest.book_request_discount === null ? 'indigo indigo-text' : 'pink pink-text' ") {{ bookRequest.book_request_discount === null ? 'خیر' : 'بله' }}
                  .col.m12.m4
                    table.responsive-table.striped
                      tbody
                        tr
                          td معارفه :
                          td  {{ bookRequest.introduction }}
            // users view card data ends
            // users view card details start
            .card
              .card-content
                //- .row.indigo.lighten-5.border-radius-4.mb-2
                //-   .col.s12.m6.users-view-timeline
                //-     h6.indigo-text.m-0
                //-       | تعداد دریافتی‌ها :
                //-       span.ml-2 {{ talentPaidCount }}
                //-   .col.s12.m6.users-view-timeline
                //-     h6.indigo-text.m-0
                //-       | تعداد پیام‌ها :
                //-       span.ml-2 {{ notificationCount }}
                .row
                  .col.s12.m6
                    h6.mb-2.mt-2
                      router-link.display-flex.justify-content-start.align-items-center(:to="{ name: 'show-user', props: { id: user.id } }")
                        img.z-depth-1.circle(
                          :src="[talent.image ? globals.baseURL + '/' + talent.image : '/app-assets/images/avatar/guest.png']",
                          alt="users view avatar",
                          height="64",
                          width="64"
                        )
                        i.material-icons.mr-1.ml-2 error_outline
                        | اطلاعات تولید کننده هدیو
                    table.striped
                      tbody
                        tr
                          td شناسه :
                          td.users-view-username {{ talent.id }}
                        tr
                          td شماره :
                          td.users-view-username {{ talent.no }}
                        tr
                          td نام و نام خانوادگی :
                          td.users-view-username {{ talent.name }}
                        tr
                          td موبایل :
                          td.users-view-name {{ talent.mobile }}
                        tr
                          td دارایی :
                          td.users-view-username {{ numberToCash(talent.cash) }}
                        tr
                          td دارایی قفل شده :
                          td.users-view-username {{ numberToCash(talent.block_cash) }}
                        tr
                          td دارایی خیریه :
                          td.users-view-username {{ numberToCash(talent.charity_cash) }}
                        tr
                          td ایجاد :
                          td.users-view-username {{ toPersianTime(user.created_at) }}
                        tr
                          td بروزرسانی :
                          td.users-view-username {{ toPersianTime(user.updated_at) }}
                  .col.s12.m6
                    h6.mb-2.mt-2
                      router-link.display-flex.justify-content-start.align-items-center(:to="{ name: 'show-user', props: { id: user.id } }")
                        img.z-depth-1.circle(
                        :src="[user.image ? globals.baseURL + '/' + user.image : '/app-assets/images/avatar/guest.png']",
                        alt="users view avatar",
                        height="64",
                        width="64"
                      )
                        i.material-icons.mr-1.ml-2 error_outline
                        | اطلاعات درخواست کننده هدیو
                    table.striped
                      tbody
                        tr
                          td شناسه :
                          td.users-view-username {{ user.id }}
                        tr
                          td شماره :
                          td.users-view-username {{ user.no }}
                        tr
                          td نام و نام خانوادگی :
                          td.users-view-username {{ user.first }} {{ user.last }}
                        tr
                          td موبایل :
                          td.users-view-name {{ user.mobile }}
                        tr
                          td دارایی :
                          td.users-view-username {{ numberToCash(user.cash) }}
                        tr
                          td ایجاد :
                          td.users-view-username {{ toPersianTime(user.created_at) }}
                        tr
                          td بروزرسانی :
                          td.users-view-username {{ toPersianTime(user.updated_at) }}
            // users view card details ends
</template>

<script>
import VueCookies from "vue-cookies";
import BodyComponent from "@/layouts/BodyComponent";
import BookRequestService from "@/services/BookRequestService.js";
import { globals } from "@/common/global/globals";
import { toPersianTime } from "@/common/utils/moment";
export default {
  props: {
    id: { type: String },
  },
  components: {
    BodyComponent,
  },
  data() {
    return {
      token: "",
      bookRequest: {},
      user: {},
      talent: {},
      breadcrumpTitle: "",
      globals,
      toPersianTime,
    };
  },
  methods: {
    // get data by id
    getBookRequestData() {
      this.token = VueCookies.get("adminToken");
      BookRequestService.getAllBookRequestWithTalentAndUser(this.token, 1, { bookRequestId: this.id })
        .then(response => {
          this.bookRequest = response.data.items[0];
          this.user = this.bookRequest.book_request_user;
          this.talent = this.bookRequest.book_request_talent;
          console.log(this.bookRequest);
          this.breadcrumpTitle = `نمایش درخواست ویدیو برای ${this.bookRequest.target_name}`;
        })
        .catch(() => this.$router.go(-1));
    },
    numberToCash(number) {
      const cash = new Number(number).toLocaleString("fa-IR");
      return cash !== "ناعدد" ? cash : 0;
    },
  },
  created() {
    this.getBookRequestData();
  },
};
</script>

<style lang="scss" scoped>
@import "/app-assets/vendors/select2/select2.min.css";
@import "/app-assets/vendors/select2/select2-materialize.css";
.media-body {
  width: fit-content;
  width: -webkit-fill-available;
}
</style>
