<template lang="pug">
BodyComponent
  #main
    .row
      C-Breadcrump(
        title="مدیریت سفارش‌ها"
        start="خانه"
        :startPath="{ name: 'home-page' }"
        middle="سفارش‌ها"
        :middlePath="{ name: 'all-book_requests' }"
        last="مدیریت سفارش‌ها"
      )
      .col.s12
        .container
          section.users-list-wrapper.section

            // users list filter
            .users-list-filter
              .card-panel
                .row
                  form(@submit.prevent="submitFilter")
                    .col.s12.m6.l3.w-20(v-for="filterData in filterDatas")
                      label(for="users-list-verified") {{ filterData.title }}
                      .input-field
                        .select-wrapper
                          select.form-control(:name="filterData.name" :id="filterData.name" tabindex="-1")
                            option(v-for="option in filterData.options" :value="option.value") {{ option.title }}
                    .col.s12.m6.l3.display-flex.align-items-center.show-btn.w-20
                      button.btn.btn-block.indigo(type="submit") فیلتر

            .users-list-table
              .card
                .card-content
                  // datatable start
                  D-Table(
                    :AllData="bookRequests",
                    :headers=[
                      { title: 'شماره', key: 'no' },
                      { title: 'نام تلنت', key: 'book_request_talent', secondKey: 'name' },
                      { title: 'سفارش دهنده', key: 'target_name' },
                      { title: 'زمان ثبت', key: 'created_at', isDate: true },
                      { title: 'مبلغ', key: 'request_price' },
                      { title: 'پلن', key: 'plan_type' },
                    ],
                    :moreButtons=[
                      { name: 'ویرایش', event: 'editBookRequests', icon: 'edit' },
                      { name: 'نمایش', event: 'showBookRequests', icon: 'remove_red_eye' },
                      { name: 'حذف', event: 'deleteBookRequests', icon: 'delete' }
                    ],
                    :pagination="pagination",
                    @call-page="callPage",
                    @button-events="callButtonEvents"
                  )
                  // datatable ends
          // users list ends
</template>

<script>
import VueCookies from "vue-cookies";
import { Toast } from "@/common/plugins/sweetalert";
import { TableScripts } from "@/common/global/scripts";
import { addStaticScripts } from "@/common/utils/addStatics";
import BookRequestService from "@/services/BookRequestService.js";
import BodyComponent from "@/layouts/BodyComponent";
export default {
  components: {
    BodyComponent,
  },
  data() {
    return {
      bookRequests: {},
      token: "",
      filterDatas: [
        {
          name: "type",
          title: "نوع",
          options: [
            { value: "PUBLIC", title: "PUBLIC" },
            { value: "PRIVATE", title: "PRIVATE" },
          ],
        },
        {
          name: "target",
          title: "هدف",
          options: [
            { value: "FRIEND", title: "FRIEND" },
            { value: "SELF", title: "SELF" },
          ],
        },
        {
          name: "isAccepted",
          title: "وضعیت",
          options: [
            { value: "NOT_ANSWERED", title: "NOT_ANSWERED" },
            { value: "ACCEPTED", title: "ACCEPTED" },
            { value: "REJECTED", title: "REJECTED" },
          ],
        },
        {
          name: "plan_type",
          title: "پلن",
          options: [
            { value: "GOLD", title: "GOLD" },
            { value: "SILVER", title: "SILVER" },
            { value: "BRONZE", title: "BRONZE" },
          ],
        },
      ],
      formData: {},
      pagination: { currentPage: 1 },
    };
  },
  methods: {
    // call needed function to apply filter
    submitFilter(submitEvent) {
      this.getFormData(submitEvent);
      this.getBookRequestWithFilter();
    },
    // fill or update form data
    getFormData(submitEvent) {
      const data = submitEvent.target.elements;
      this.formData = {
        type: data.type.value,
        target: data.target.value,
        isAccepted: data.isAccepted.value,
        plan_type: data.plan_type.value,
      };
    },
    getBookRequestWithFilter() {
      BookRequestService.getAllBookRequestWithTalentAndUser(this.token, this.pagination.currentPage, this.formData)
        .then(response => (this.bookRequests = response.data))
        .catch(error => {
          Toast("error", "مشکلی در اعامل فیلتر رخ داد.");
          console.log(`there was an error: ${error}`);
        });
    },
    callPage(page) {
      this.token = VueCookies.get("adminToken");
      BookRequestService.getAllBookRequestWithTalentAndUser(this.token, page)
        .then(response => {
          this.bookRequests = response.data;
          this.pagination = response.data.meta;
        })
        .catch(error => {
          Toast("error", "مشکلی در دریافت اطلاعات لیست رخ داد.");
          console.log(`there was an error: ${error}`);
        });
    },
    callButtonEvents(data) {
      switch (data.event) {
        case "editBookRequests":
          this.$router.push({ name: "edit-book_requests", params: { id: data.id } });
          break;
        case "showBookRequests":
          this.$router.push({ name: "show-book_requests", params: { id: data.id } });
          break;
        case "deleteBookRequests":
          this.deleteBookRequests(data.id);
          break;

        default:
          Toast("error", "مشکلی در اجرای ایونت وضعیت رخ داد.");
          break;
      }
    },
    deleteBookRequests(id) {
      BookRequestService.setBookRequestDeleteTime(this.token, id)
        .then(() => {
          this.callPage(this.pagination.currentPage);
        })
        .catch(error => {
          Toast("error", "مشکلی در دریافت اطلاعات ایونت رخ داد.");
          console.log(`there was an error: ${error}`);
        });
    },
  },
  // created() {
  //   this.callPage(this.pagination.currentPage);
  // },
  mounted() {
    this.callPage(this.pagination.currentPage);
    addStaticScripts(TableScripts);
  },
};
</script>

<style lang="scss" scoped>
@import "/app-assets/vendors/data-tables/css/jquery.dataTables.min.css";
@import "/app-assets/vendors/data-tables/extensions/responsive/css/responsive.dataTables.min.css";
@import "/app-assets/css-rtl/pages/page-users.min.css";
.w-20 {
  width: 20% !important;
}
</style>
