<template lang="pug">
// BEGIN: SideNav
aside.sidenav-main.nav-expanded.nav-lock.nav-collapsible.sidenav-light.sidenav-active-square
  .brand-sidebar
    h1.logo-wrapper
      router-link.brand-logo.darken-1(:to="{ name: 'home-page' }")
        img.hide-on-med-and-down(src="/app-assets/images/logo/materialize-logo-color.png", alt="materialize logo")
        img.show-on-medium-and-down.hide-on-med-and-up(
          src="/app-assets/images/logo/materialize-logo.png",
          alt="materialize logo"
        )
        span.logo-text.hide-on-med-and-down {{ AppName }}
      //- a.navbar-toggler(href="#")
        //- i.material-icons radio_button_checked
  ul#slide-out.sidenav.sidenav-collapsible.leftside-navigation.collapsible.sidenav-fixed.menu-shadow(
    data-menu="menu-navigation",
    data-collapsible="menu-accordion"
  )
    <S-DropDown-Item itemName="داشبورد" :link="{ name: 'home-page' }" icon="dashboard" translateKey="Dashboard" />

    <S-DropDown title="سفارش‌ها" translateKey="BookRequests Menu" icon="book">
      <S-DropDown-Item itemName="مدیریت سفارش‌ها" :link="{ name: 'all-book_requests' }" translateKey="AllBookRequests Menu" />
    </S-DropDown>

    <S-DropDown title="کاربران" translateKey="Person Menu" icon="account_circle">
      <S-DropDown-Item itemName="تلنت‌ها" :link="{ name: 'all-talents' }" translateKey="Talent Menu" />
      <S-DropDown-Item itemName="کاربرها" :link="{ name: 'all-users' }" translateKey="User Menu" />
      <S-DropDown-Item itemName="خیریه‌ها" :link="{ name: 'all-charities' }" translateKey="Charity Menu" />
      <S-DropDown-Item itemName="ادمین‌ها" :link="{ name: 'all-admins' }" translateKey="Admin Menu" />
    </S-DropDown>

    <S-DropDown title="نوتیفیکیشن‌ها" translateKey="Notification Menu" icon="notifications_active" :notifCount="notification_count">
      <S-DropDown-Item itemName="نوتیفیکیشن‌های شما" :link="{ name: 'your-notifications' }" translateKey="Your Notify Menu" />
    </S-DropDown>

    li(:class="{ active: isActive }")
      a(@click="logout")
        i.material-icons exit_to_app
        span(data-i18n="logout") خروج

  .navigation-background
  a.sidenav-trigger.btn-sidenav-toggle.btn-floating.btn-medium.waves-effect.waves-light.hide-on-large-only(
    href="#",
    data-target="slide-out"
  )
    i.material-icons menu
// END: SideNav
</template>

<script>
import VueCookies from "vue-cookies";
import ServerInfoService from "@/services/ServerInfoService.js";
export default {
  data() {
    return {
      token: "",
      notification_count: 0,
    };
  },
  props: {
    AppName: { type: String },
  },
  methods: {
    getServerInfo() {
      this.token = VueCookies.get("adminToken");
      ServerInfoService.getServerInfo(this.token).then(
        response => (this.notification_count = response.data.notification_count),
      );
    },
    logout() {
      VueCookies.remove("adminToken");
      this.$router.go("{ name: 'login-page' }");
    },
  },
  created() {
    this.getServerInfo();
  },
};
</script>
