import { apiClient } from "./Service";

export default {
  async getAllBookRequestWithTalentAndUser(token, page, data) {
    let uri = `/admins/book/get_all?page=${page}&limit=10`;
    if (data && data.bookRequestId) uri += `&bookRequestId=${data.bookRequestId}`;
    if (data && data.type) uri += `&type=${data.type}`;
    if (data && data.target) uri += `&target=${data.target}`;
    if (data && data.isAccepted) uri += `&isAccepted=${data.isAccepted}`;
    if (data && data.plan_type) uri += `&plan_type=${data.plan_type}`;
    console.log("data", data);
    console.log("uri", uri);
    return await apiClient.get(uri, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },

  async setBookRequestDeleteTime(token, id) {
    return await apiClient.get(`/admins/book/set/delete/${id}`, {
      headers: {
        authorization: `Bearer ${token}`,
      },
    });
  },
};
